﻿.model-inspector {

    .inspector-section {
        border: 1px solid $light-grey;
        
        .inspector-content {
            padding: 10px 20px;
        }
    }

    .inspector-header {
        background: $blue;
        padding: 10px;
        color: $white;
        font-size: 20px;
        font-weight: bold;

        i {
            float: right;
            cursor: pointer;

            &:hover {
                opacity: 0.6;
            }
        }
    }


}